@import "https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.11.2/css/all.min.css";
@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap";
* {
  box-sizing: border-box;
  font-family: Roboto;
}

html, body {
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  grid-gap: 0;
  grid-template: "header main" 1fr
                 "footer footer"
                 / 2fr 4fr;
  display: grid;
}

.nosp {
  white-space: nowrap;
}

footer, main, header {
  min-height: 20vh;
  padding: 1rem;
}

main, header {
  color: #000;
  background-color: #fff;
}

header {
  grid-area: header;
  place-items: center;
  display: grid;
}

header .title {
  letter-spacing: .3rem;
  place-items: start center;
  margin: 0;
  font-weight: 500;
  display: grid;
}

header .title .logo {
  width: 250px;
  height: 250px;
  opacity: .9;
  background-image: url("logo.8d84c320.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  place-items: end center;
  display: grid;
}

header .title .logo .link, header .title .logo .link > a {
  letter-spacing: 0;
  color: #a72d31;
  padding-bottom: 1.5rem;
  font-weight: 600;
  text-decoration: none;
}

main {
  grid-area: main;
  place-items: center start;
  display: grid;
}

main .content {
  border-spacing: .3rem;
  max-width: 800px;
  border-left: 2px solid #fff3;
  padding-left: 5vw;
}

main .content td {
  vertical-align: top;
  background-color: #f7f7f7;
  padding: .5rem 1rem;
}

footer {
  color: #b3b3b3;
  background-color: #000;
  grid-area: footer;
  grid-template-columns: 1fr 2fr;
  padding: 1rem 0;
  display: grid;
}

footer .logo {
  justify-content: center;
  align-items: center;
  display: flex;
}

footer .logo a {
  color: inherit;
  text-decoration: none;
}

footer .logo .copy {
  text-align: right;
  padding-right: 1rem;
}

footer .logo .vsegda {
  height: 60px;
  width: 60px;
  opacity: .7;
  background-image: url("vsegda.968f1c61.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

footer .info {
  height: 100%;
  border-left: 2px solid #fff3;
  grid-template-columns: auto auto;
  margin-left: 1rem;
  padding: 2rem 2rem 2rem 5vw;
  font-size: .9rem;
  display: grid;
}

footer .info ul {
  margin: 0;
}

footer .info ul li {
  padding-top: .25rem;
  padding-bottom: .25rem;
}

footer .info ul.contacts {
  padding: 0;
  list-style: none;
}

footer .info ul.contacts li {
  align-items: center;
  display: flex;
}

footer .info ul.contacts li i.fa {
  width: 2rem;
  min-width: 2rem;
  justify-content: center;
  margin-left: -.5rem;
  font-size: 1rem;
  display: inline-flex;
}

footer .info ul.contacts li a {
  color: inherit;
  text-decoration: none;
}

footer .info ul.contacts li a:hover {
  text-decoration: underline;
}

footer .info ul.docs {
  padding: 0 1rem;
}

@media screen and (max-width: 768px) {
  header, main, footer {
    padding: 0;
  }

  body {
    grid-template: "header"
                   "main" 1fr
                   "footer"
                   / 1fr;
  }

  body main {
    place-items: start;
    font-size: .9rem;
  }

  body main .content {
    width: 100%;
    max-width: unset;
    border: none;
    margin-bottom: 1rem;
    padding: 0;
  }

  body footer {
    grid-template-columns: unset;
  }

  body footer .info {
    max-width: unset;
    border: none;
    grid-template-columns: auto;
    grid-row-start: 1;
    margin: 0;
    padding: 1rem;
  }

  body footer .info ul.docs {
    margin-top: 1rem;
  }

  body footer .logo {
    opacity: .7;
    border-top: 2px solid #fff3;
    justify-content: flex-end;
    padding: 1rem;
    font-size: .9rem;
  }

  body footer .logo .vsegda {
    width: 40px;
    height: 40px;
  }
}

